import { ReactNode } from "react";
import { Link } from "react-router-dom";
import cn from "classnames";
import { Book } from "../../../../../../routes/book";
import classes from "./style.module.css";

interface IProps {
  children?: ReactNode;
  linkClassName?: string;
  linkDesc?: string;
  linkTitle?: string;
  linkTo?: Book;
  title?: string;
}

export const Header = ({
  children,
  linkClassName,
  linkDesc,
  linkTitle,
  linkTo,
  title = "livedune",
}: IProps) => {
  return (
    <header className={classes.header}>
      <Link className={classes.logo} to={Book.root}>
        {title}
      </Link>

      <div className={classes.wrap}>
        {linkDesc && <p className={classes.desc}>{linkDesc}</p>}

        {linkTitle && linkTo && (
          <Link className={cn(classes.link, linkClassName)} to={linkTo}>
            {linkTitle}
          </Link>
        )}

        { children }
      </div>
    </header>
  );
};
