import { HTMLAttributes, ReactNode } from "react";
import cn from "classnames";
import { Authentication } from "../../../../../authentication";
import classes from "./style.module.css";

interface IProps extends Omit<HTMLAttributes<HTMLElement>, "className"> {
  authentication?: boolean;
  children?: ReactNode;
  className?: string;
  desc?: string | ReactNode;
  descImg?: string;
  descImgAlt?: string;
  title: string;
  titleImg?: string;
  titleImgAlt?: string;
  gap?: string;
}

export const Main = ({
  authentication = false,
  children,
  className,
  desc,
  descImg,
  descImgAlt,
  title,
  titleImg,
  titleImgAlt,
  gap,
}: IProps) => {
  return (
    <main className={cn(classes.main, className)}>
      <div className={classes.titleWrap}>
        {titleImg && titleImgAlt && (
          <img
            className={classes.titleImg}
            src={`./${titleImg}`}
            alt={titleImgAlt}
          />
        )}

        <h1 className={classes.title}>{title}</h1>

        {desc && typeof desc === 'string' ? (
          <p className={classes.desc}>
            {desc}&nbsp;
            {descImg && (
              <img alt={descImgAlt} className={classes.img} src={`./${descImg}`} />
            )}
          </p>
        ) : desc }

      </div>

      {authentication && <Authentication />}

      {gap && <p className={classes.gap}>{gap}</p>}

      {children}
    </main>
  );
};
