import classes from "./style.module.css";

const config = [
  { title: "Facebook", img: "facebook-icon.svg" },
  { title: "Google", img: "google-icon.svg" },
];

export const Cards = () => {
  const renderOptions = config.map((card) => (
    <div className={classes.card} key={card.title}>
      <img
        alt={card.title.toLowerCase()}
        className={classes.cardIcon}
        src={`./${card.img}`}
      />

      <a
        className={classes.cardLink}
        href={`https://www.${card.title.toLowerCase()}.com/`}
        target="_blank"
        rel="noreferrer noopener"
      >
        Войти через&nbsp;{card.title}
      </a>
    </div>
  ));

  return (
    <>
      {renderOptions}
    </>
  );
};
