import classes from "./style.module.css";

interface IProps {
  errors: string[];
}

export const FormErrors = ({ errors }: IProps) => {
  return (
    <p className={classes.errorText}>
      {errors
        .filter((errMessage) => errMessage)
        .join(", ")
        .replace(", Неверный email или пароль", "")}
    </p>
  );
};
