import { FC, lazy, Suspense } from "react";
import {
  MemoryRouter as Router,
  Route,
  Switch,
} from "react-router-dom";
import { Spinner } from "../features/ui";
import { Book } from "./book";

const Login = lazy(() => import("../features/login"));
const Registration = lazy(() => import("../features/registration"));
const ConfirmEmail = lazy(() => import("../features/confirm-email"));
const ResendEmail = lazy(() => import("../features/resend-email"));
const Welcome = lazy(() => import("../features/welcome"));
const RestorePassword = lazy(() => import("../features/restore-password"));
const EmailSend = lazy(() => import("../features/email-send"));
const Error404Page = lazy(() => import("../features/ui/features/error404"));

export const Routes: FC = () => (
  <Router>
    <Suspense fallback={<Spinner />}>
      <Switch>
        <Route exact component={Login} path={Book.root} />

        <Route component={Registration} path={Book.registration} />

        <Route component={ConfirmEmail} path={Book.confirmEmail} />

        <Route component={ResendEmail} path={Book.resendEmail} />

        <Route component={Welcome} path={Book.welcome} />

        <Route component={RestorePassword} path={Book.restorePassword} />

        <Route component={EmailSend} path={Book.emailSend} />

        <Route component={Error404Page} path="*" />
      </Switch>
    </Suspense>
  </Router>
);
